body {
  font-family: "Playfair Display", serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #F3F3F3;
}
@media (min-width: 768px) {
  body {
    font-size: 1.8rem;
  }
}

a {
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 900;
  color: #F3F3F3;
  transition-duration: 0.5s;
  transition-property: font-size;
}
a.activo, a:hover {
  color: #860A35;
  padding: 0 0.5rem;
  border-radius: 0.5rem;
  background-color: rgba(255, 255, 255, 0.5098039216);
}

a:hover {
  font-size: 2rem;
}
@media (min-width: 768px) {
  a:hover {
    font-size: 1.6rem;
  }
}
@media (min-width: 992px) {
  a:hover {
    font-size: 2rem;
  }
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

span {
  font-weight: bold;
}

.contenedor {
  width: 75%;
  max-width: 120rem;
  margin: 0 auto;
}

/* Para el carrito */
th,
td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #E1E1E1;
}

th:first-child,
td:first-child {
  padding-left: 0;
}

th:last-child,
td:last-child {
  padding-right: 0;
}

table {
  margin-bottom: 2.5rem;
}

.text-center {
  text-align: center;
}

.encabezados {
  margin: 0 auto;
  width: 90%;
  color: #fff;
  background: #860A35;
  text-transform: capitalize;
  clip-path: polygon(100% 0, 96% 50%, 100% 100%, 0 100%, 4% 50%, 0 0);
}

.btn {
  padding: 0.5rem 3rem;
  border-radius: 0.5rem;
  border: none;
  background-color: #fff;
  color: #860A35;
  text-transform: uppercase;
  font-size: 1.6rem;
  letter-spacing: 2px;
  transition-duration: 0.4s;
  transition-property: transform;
}

.btn:hover {
  transform: scale(1.1);
}

.contacto__input {
  color: #860A35;
  border: 2px solid #860A35;
  background-color: #fff;
  display: inline-block;
  border-radius: 1.3rem;
  padding: 0.5rem 2.5rem;
  outline: none;
}

.mensajeAlerta {
  color: #860A35;
  border: 2px solid #860A35;
  background-color: #fff;
  border-radius: 1.3rem;
  padding: 0.5rem 2rem;
  font-weight: 600;
  font-size: 1.6rem;
  text-align: center;
}

.correcto {
  color: green;
  border: 2px solid green;
}

.error {
  border: 2px solid red;
  color: red;
}

/* Utilidades para el carrito */
.u-full-width {
  width: 100%;
  box-sizing: border-box;
}

.u-max-full-width {
  max-width: 100%;
  box-sizing: border-box;
}

.u-pull-right {
  float: right;
}

.u-pull-left {
  float: left;
}

.carrito__vaciar {
  display: inline-block;
  color: #860A35;
  text-align: center;
  font-size: 14px;
  font-weight: 600;
  line-height: 2;
  outline: none;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #fff;
  border-radius: 1rem;
  border: 1px solid #860A35;
  cursor: pointer;
}

.button:hover {
  font-size: 14px;
}

.eliminar-producto {
  background-color: red;
  border-radius: 50%;
  padding: 5px 10px;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.eliminar-producto:hover {
  font-size: unset;
  background-color: red;
  border-radius: 50%;
  padding: 5px 10px;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

h1, h2, h3 {
  text-transform: capitalize;
  font-family: "Playfair Display", serif;
  letter-spacing: 3px;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2.2rem;
}

h3 {
  font-size: 1.6rem;
}

@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 3rem;
  }
  h3 {
    font-size: 2rem;
  }
}
.header {
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}

@media (min-width: 768px) {
  .header__titulo {
    padding: 10rem 0 0 0;
  }
}
.header__titulo .header__texto {
  color: #860A35;
  text-shadow: 0 0 5px #fff;
}

.header-inicio {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/build/img/nosotros-hero.jpg);
}

.header-nosotros {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/build/img/hero1.jpg);
}

.header-proceso {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/build/img/proceso-hero.jpg);
}

.header-productos {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/build/img/antojos-1.jpg);
}

.header__contacto {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/build/img/cheesecake.jpg);
}

.navegacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.8rem;
}
@media (min-width: 768px) {
  .navegacion {
    font-size: 1.4rem;
    flex-direction: row;
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .navegacion {
    font-size: 1.8rem;
  }
}

.header__barra {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-around;
  }
}
.header__barra .header__logo {
  font-family: "Petit Formal Script", cursive;
  letter-spacing: 4px;
  font-size: 3rem;
  font-weight: 600;
  color: #fff;
  text-shadow: 1px 1px 5px #860A35;
  width: 15rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .header__barra .header__logo {
    width: 8rem;
    font-size: 2rem;
  }
}

.principal {
  background-color: #fff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .principal {
    padding: 10rem 2rem;
    margin-top: -10rem;
    position: relative;
    border-radius: 1rem;
  }
}
.principal .principal__descripcion {
  margin: 2.5rem 0;
}

.galeria {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
  margin: 0 auto;
  width: 90%;
}
.galeria .galeria__categoria {
  border-radius: 1rem;
  overflow: hidden;
  border: 2px solid #860A35;
}
.galeria .galeria__categoria .galeria__img {
  transition-duration: 0.5s;
  transition-property: transform;
  width: 100%;
  height: 25rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .galeria .galeria__categoria .galeria__img {
    height: 35rem; /* Mantener para el slider */
  }
}
.galeria .galeria__categoria .galeria__img:hover {
  transform: scale(1.1);
}
.galeria .galeria__categoria .galeria__descripcion {
  background-color: #860A35;
  color: #fff;
  letter-spacing: 3px;
  padding: 1rem 0;
  position: relative;
  display: block;
}

