.galeria {
  width: 90%;
  position: relative;
  overflow: hidden;
}
.galeria .galeria__categoria {
  height: 100%;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  box-sizing: border-box; /* Con esto no se desborda */
}
.galeria .swiper-button-next,
.galeria .swiper-button-prev {
  color: #860A35;
  text-shadow: 1px 1px 1px #fff;
}
.galeria .swiper-pagination {
  position: relative;
}
.galeria .swiper-pagination-bullet {
  background-color: pink;
  opacity: 0.8;
}
.galeria .swiper-pagination-bullet-active {
  background-color: #860A35;
}

.sucursales {
  background-image: linear-gradient(rgba(255, 192, 203, 0.259), rgba(255, 192, 203, 0.259)), url(/build/img/watercolor.png);
  background-repeat: repeat;
  background-size: cover;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sucursales {
    margin-top: -10rem;
    padding: 15rem 2rem 15rem 2rem; /* Checar el padding bottom */
  }
}
.sucursales .sucursales__contenido {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
@media (min-width: 768px) {
  .sucursales .sucursales__contenido {
    gap: 4rem;
  }
}
@media (min-width: 768px) {
  .sucursales .sucursales__contenido .sucursales__texto {
    margin: 0 auto;
    max-width: 80%;
  }
}
.sucursales .sucursales__contenido .sucursales__imagen {
  border-radius: 1rem;
  overflow: hidden;
  border: 3px solid #860A35;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .sucursales .sucursales__contenido .sucursales__imagen {
    max-width: 80%;
  }
}
@media (min-width: 992px) {
  .sucursales .sucursales__contenido .sucursales__imagen {
    max-width: 65%;
  }
}

.testimoniales .testimoniales__contenido {
  background-color: #fff;
  padding: 5rem 3rem;
  border-radius: 1rem; /* Convervar? */
}
@media (min-width: 768px) {
  .testimoniales .testimoniales__contenido {
    margin-top: -10rem;
    padding: 10rem 0;
  }
}
.testimoniales .testimoniales__contenido .testimoniales__titulo {
  margin-bottom: 5rem; /* Necesario? */
  padding: 0 1rem;
}
.testimoniales .testimoniales__contenido .testimoniales__testimonial {
  background-color: #F3F3F3;
  padding: 1rem;
  margin: 0 auto;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .testimoniales .testimoniales__contenido .testimoniales__testimonial {
    max-width: 50%;
    padding: 4rem;
  }
}
.testimoniales .testimoniales__contenido .testimoniales__testimonial .testimoniales__autor {
  margin-top: 2rem;
  text-align: right;
  font-family: cursive;
}

.footer {
  background-color: #860A35;
}
.footer .footer__copyright {
  padding: 1.5rem;
  color: #fff;
}

.footer__contenido {
  padding: 3rem;
  background-color: #fff;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
@media (min-width: 768px) {
  .footer__contenido {
    flex-direction: row;
    justify-content: space-around;
    gap: 1rem;
  }
}
@media (min-width: 992px) {
  .footer__contenido .footer__contenedor {
    width: 30rem;
  }
}
.footer__contenido .footer__contenedor h3 {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer__contenido .footer__contenedor h3 {
    padding: 0 1rem;
    font-size: 1.6rem;
  }
}
@media (min-width: 992px) {
  .footer__contenido .footer__contenedor h3 {
    font-size: 2rem;
  }
}
.footer__contenido .footer__contenedor a {
  color: #282828;
  text-transform: capitalize;
  font-weight: 400;
}

.nosotros {
  padding: 5rem 3rem;
  background-color: #fff;
}
@media (min-width: 768px) {
  .nosotros {
    padding: 10rem 2rem 5rem 2rem;
    margin-top: -10rem;
    position: relative;
    border-radius: 1rem;
  }
}
.nosotros .nosotros__titulo {
  margin-bottom: 5rem;
  padding: 0 1rem;
}
.nosotros .nosotros__contenido {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 992px) {
  .nosotros .nosotros__contenido {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
  }
}
.nosotros .nosotros__contenido .nosotros__imagen {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .nosotros .nosotros__contenido .nosotros__imagen {
    width: 30rem;
    height: 40rem;
  }
}
@media (min-width: 992px) {
  .nosotros .nosotros__contenido .nosotros__imagen {
    transform: rotate(-15deg) scale(0.8);
  }
}
@media (min-width: 768px) {
  .nosotros .nosotros__contenido .nosotros__imagen img {
    padding: 2rem;
    box-shadow: 1rem 1rem 5px -7px rgba(133, 10, 53, 0.302);
  }
}
.nosotros .nosotros__contenido .nosotros__parrafos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 992px) {
  .nosotros .nosotros__contenido .nosotros__parrafos {
    width: 65%;
  }
}
.nosotros .nosotros__contenido .nosotros__parrafos .nosotros__texto {
  padding: 1rem;
}

.proceso {
  background-color: #fff;
  padding: 5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .proceso {
    padding: 10rem 2rem;
    margin-top: -10rem;
    position: relative;
  }
}
.proceso .proceso__contenedor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 0 2rem;
}

.proceso__proceso {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
@media (min-width: 768px) {
  .proceso__proceso {
    flex-direction: row;
  }
  .proceso__proceso:nth-child(even) .proceso__contenido {
    order: 1;
  }
  .proceso__proceso .proceso__contenido,
  .proceso__proceso .proceso__imagen {
    flex: 1;
  }
}
.proceso__proceso .proceso__imagen .proceso__img {
  width: 100%;
  object-fit: cover;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .proceso__proceso .proceso__imagen .proceso__img {
    height: 100%;
  }
}
@media (min-width: 992px) {
  .proceso__proceso .proceso__imagen .proceso__img {
    height: 30rem;
  }
}
.proceso__proceso .proceso__contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
.proceso__proceso .proceso__contenido .proceso__descripcion {
  margin-bottom: 2rem;
  padding: 0 1.5rem;
}
.proceso__proceso .proceso__contenido .proceso__texto {
  max-width: 90%;
}

.productos {
  padding: 5rem 2rem;
  background-color: #fff;
}
@media (min-width: 768px) {
  .productos {
    padding: 10rem 2rem 5rem 2rem;
    margin-top: -10rem;
    position: relative;
    border-radius: 1rem;
  }
}
.productos .productos__categoria {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
.productos .productos__categoria .productos__seccion {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
@media (min-width: 768px) {
  .productos .productos__categoria .productos__seccion {
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
.productos .productos__categoria .productos__seccion .productos__producto {
  border-radius: 1rem;
  overflow: hidden;
  border: 2px solid #860A35;
}
@media (min-width: 768px) {
  .productos .productos__categoria .productos__seccion .productos__producto {
    width: 45%;
  }
}
@media (min-width: 992px) {
  .productos .productos__categoria .productos__seccion .productos__producto {
    width: 30%;
  }
}
.productos .productos__categoria .productos__img {
  transition-duration: 0.5s;
  transition-property: transform;
  width: 100%;
  height: 25rem;
  object-fit: cover;
}
.productos .productos__categoria .productos__img:hover {
  transform: scale(1.1);
}
.productos .productos__categoria .productos__descripcion {
  background-color: #860A35;
  color: #fff;
  letter-spacing: 3px;
  padding: 1rem 0;
  position: relative;
}

.carrito {
  position: relative;
}
.carrito .carrito__contenido {
  display: none;
}

.carrito:hover .carrito__contenido {
  display: block;
  position: absolute;
  right: -550%;
  top: 100%;
  z-index: 1;
  background-color: #fff;
  padding: 20px;
  min-height: 400px;
  min-width: 300px;
}
@media (min-width: 768px) {
  .carrito:hover .carrito__contenido {
    right: 0;
  }
}

.contacto {
  background-color: #F3F3F3;
}
.contacto .contacto__contenedor {
  background-color: #fff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contacto .contacto__contenedor {
    padding: 10rem 2rem 5rem 2rem;
    margin-top: -10rem;
    position: relative;
    border-radius: 1rem;
  }
}
.contacto .contacto__contenedor .contacto__header {
  margin-bottom: 5rem;
}

.contacto__form {
  margin: 0 auto;
  max-width: 90%;
}
@media (min-width: 768px) {
  .contacto__form {
    max-width: 80%;
  }
}
@media (min-width: 992px) {
  .contacto__form {
    max-width: 70%;
  }
}
.contacto__form .contacto__inputs {
  display: flex;
}
.contacto__form .contacto__inputs .contacto__row-1, .contacto__form .contacto__inputs .contacto__row-2 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
}
@media (min-width: 768px) {
  .contacto__form .contacto__inputs .contacto__row-1, .contacto__form .contacto__inputs .contacto__row-2 {
    flex-direction: row;
  }
}
.contacto__form .contacto__inputs .contacto__row-1 .contacto__input, .contacto__form .contacto__inputs .contacto__row-2 .contacto__input {
  width: 100%;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .contacto__form .contacto__inputs .contacto__row-1 .contacto__input, .contacto__form .contacto__inputs .contacto__row-2 .contacto__input {
    font-size: 1.8rem;
  }
}
.contacto__form .contacto__submit {
  max-width: 45%;
  margin: 0 auto;
  transition-duration: 0.4s;
  transition-property: color background-color;
}
.contacto__form .contacto__submit:hover {
  background-color: #860A35;
  color: #fff;
}

.contacto__form,
.contacto__inputs {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contacto__input::placeholder,
.contacto__msj::placeholder,
.contacto__submit {
  color: #860A35;
}

